@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.MonitorSection {
    display: flex;
    width: 100%;
    height: 100%;
}

.MonitorVid {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.MonitorVid video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.MonitorImages {
    position: absolute;
    z-index: 100;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    /* border: 1px solid red; */
}

.BtnArcadeSec {
    position: relative;
    display: inline-block;
}

.BtnArcade {
    text-align: center;
    display: inline-block;
    margin: 5px;
    font-family: 'Press Start 2p';
    font-weight: bold;
    font-size: 40px;
    padding: 10px 0 10px 10px;
    background-color: lightgray;
    backdrop-filter: blur(10px);
    text-shadow: -1px -1px black, 1px 1px white;
    color: gray;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    -o-border-radius: 7px;
    border-radius: 7px;
    box-shadow: 0 .2em rgb(39, 39, 39);
    cursor: pointer;
}


.BtnArcade:active {
    box-shadow: none;
    position: relative;
    top: .2em;
}

.Badge {
    position: absolute;
    bottom: -7px;
    right: -10px;
    background-color: #FF0000;
    color: #FFFFFF;
    font-size: 16px;
    font-weight: 700;
    padding: 5px 10px;
    border-radius: 5px;
    text-transform: uppercase;
    transform: rotate(-20deg);
}

.LogoCenter {
    display: flex;
}

.MonitorRex {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 100;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    width: 400px;
    height: 400px;
    /* border: 1px solid red; */
}

.MonitorRex img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}